import { mapState, mapActions } from 'vuex'
export default {
  middleware: ['pagePermission'],
  components: {
    CrudDataTable: () => import('@/components/crud/CrudDataTable')
  },
  async fetch(context) {
    await context.store.dispatch(
      'notifications/readNotifications',
      context.$axios
    )
  },
  data() {
    return {
      search: '',
      selected: [],
      headers: [{ text: '', value: 'message' }],
      item: { email: '' }
    }
  },
  computed: {
    ...mapState(['notifications'])
  },
  methods: {
    ...mapActions('notifications', [
      'addNotification',
      'updateNotification',
      'deleteNotification'
    ]),
    addItem(item) {
      const payload = { notification: item, axios: this.$axios }
      this.addNotification(payload)
    },
    editItem(item) {
      const payload = { notification: item, axios: this.$axios }
      this.updateNotification(payload)
    },
    deleteItem(item) {
      this.deleteNotification({
        notificationId: item._id,
        axios: this.$axios
      })
    },
    message(item) {
      if (item.type === 'newsletter') {
        return 'A New Email Is Subscribed'
      }
      if (item.type === 'inquiry') {
        return 'A New Inquiry Is Received'
      }
      return 'A message from me'
    },
    route(item) {
      if (item.type === 'newsletter') {
        return '/dashboard/notifications'
      }
      return '/dashboard/notifications'
    },
    goTo(item) {
      const route = this.route(item)
      this.$router.push({ path: route })
    }
  }
}
